const header = document.querySelector('.header'); // 
const form = document.querySelector('.input__wrapper'); //  
const headerHome = document.querySelector('.home__btn'); //   Home
const headerLibrary = document.querySelector('.my-library__btn'); //   My library
const myLibraryBtnList = document.querySelector('.library-btn__list');
const logo = document.querySelector('.logo__list'); //   My library Watched  Queue

headerHome.addEventListener('click', onheaderHomeBtnClick);
headerLibrary.addEventListener('click', onMyLibraryBtnClick);
logo.addEventListener('click', onheaderHomeBtnClick);

function onheaderHomeBtnClick() {
  header.classList.remove('header__my-library'); //     My library
}

function onMyLibraryBtnClick() {
  header.classList.add('header__my-library'); //     
}
